import React, {Component} from 'react';
import styled from 'styled-components';
import EffectTable from './EffectTable';
import Color from 'color';

const data = require('../utils/effect-tables/rod-of-wondrous.json');
//#BF6A21
const colorDark = Color('#BF6A21').string();
//#ECE5B3
const colorLight = Color('#ECE5B3').string();

const ItemWrapper = styled.div `
    display: grid;
    grid-template-rows: 1.3fr 1fr;
    grid-template-columns:2fr 0.5fr 0.5fr;
    grid-row-gap:2px;
    grid-column-gap:2px;
    background-color:${colorDark};
    border: 2px solid ${colorDark};
    margin-bottom:10px;
    `;
const ItemBox = styled.div `
    background-color:${colorLight};
    display: flex;
    align-items: center;
    justify-content: start;
    padding: 5px;
    color: ${colorDark};
    font-size:medium;
    line-height: 0.8;
    padding-left:10px;
    `;
const PriceBox = ItemBox.extend `
    padding:5px 0 0 0;
    background-color:white;
    text-align:center;
    font-size:large;
    line-height: 1;
    justify-content: center;
    vertical-align:bottom;
`;
const TitleBox = ItemBox.extend `
    text-transform:uppercase;
    grid-column: 1/span 2;
    justify-content:start;
    color:${colorLight};
    font-size: x-large;
    background-color:${colorDark};
    font-weight:bold;
`;
const Description = styled.p `
    text-align:justify;
`;
class ItemTable extends Component {
    render() {
        return (
            <div>
                <ItemWrapper>
                    <TitleBox title>{this.props.title}</TitleBox>
                    <PriceBox price>
                        <b>
                            <small>PRICE</small>
                            <p>
                                {this.props.price}&nbsp;GP
                            </p>
                        </b>
                    </PriceBox>
                    <ItemBox>
                        <b>AURA&nbsp;</b>{this.props.aura}</ItemBox>
                    <ItemBox>
                        <b>CL&nbsp;</b>{this.props.cl}</ItemBox>
                    <ItemBox>
                        <b>WEIGHT&nbsp;</b>{this.props.weight}</ItemBox>
                </ItemWrapper>

                <Description>A rod of wonder is a strange and unpredictable device that randomly
                    generates any number of weird effects each time it is used. Activating the rod
                    is a standard action. Typical powers of the rod include the following.</Description>
                <EffectTable rows={data} borderColor={colorDark}/>
            </div>
        );
    }
}

export default ItemTable;


